*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  color: #2a2a2a;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, .3);
  justify-content: center;
  align-items: center;
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  max-width: 640px;
  width: 100%;
  min-height: 320px;
  background-color: #fff;
  border-radius: 4px;
  padding: 24px;
  transition: transform .2s ease-in-out;
  transform: scale(1.1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px 1px rgba(0, 0, 0, .12);
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}



